<template>
  <div class="loading" v-if="show">
    <img width="24" height="24" src="../assets/imgs/loading.gif">
    <p class="desc">{{title}}</p>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: '正在载入...'
    }
  },
  data () {
    return {
      show: false
    }
  },
  methods: {
    toshow () {
      this.show = true
    },
    tohide () {
      this.show = false
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">

  .loading
    width: 100%
    text-align: center
    display flex
    align-items center
    justify-content center
    background rgba(255,255,255,0.7)
    .desc
      line-height: 20px
      font-size: 20px
      color: rgb(88,88,88)
</style>



